import React , {useEffect} from "react";
import { NavLink } from "react-router-dom";
import {
  Box,
  Flex,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  IconButton,
  useMediaQuery,
  Text,
  
} from "@chakra-ui/react";
import {
  HamburgerIcon,
  
} from "@chakra-ui/icons";
import { useState } from "react";
import Home from "./Home";
import Skills from "./Skills";
import {motion} from 'framer-motion'
import Aos from 'aos'

function Navbar() {
  const [check, setCheck] = useState(false);
  const [isLargerThan800] = useMediaQuery("(max-width: 800px)");
  const [navbar,setNavbar]=useState(false)

 const handleChange=()=>{
  if(window.scrollY>=40){
    setNavbar(true)
  }else{
    setNavbar(false)
  }
 }
 window.addEventListener('scroll',handleChange)
 
 useEffect(() => {
  Aos.init({ duration: 2000 });
}, []);
  return (
    <Box  >
    <Box  
    
      display="flex"
      
      
      h="70px"
      p="0"
      m="auto"
      boxSizing="borderbox"
      position="fixed"
      
      
      justifyContent="space-between"
      backgroundColor={navbar ? "blue":"transperent"}
      color="white"
      w="100%"
      
     
      
    >
      <motion.Box ml={{ md: "50px", sm: "20px" }}   w="200px" animate={{x:100}} transition={{duration:2}} >
        <Text  fontSize="30px" mt="15px"  fontStyle="Fantasy" textShadow="2px 2px">
          JEEVAN ALOOR
        </Text>
      </motion.Box>
      {isLargerThan800 ? (
        <Menu>
          <MenuButton
            as={IconButton}
            aria-label="Options"
            icon={<HamburgerIcon />}
            variant="outline"
            w="70px"
            h="50px"
            mt="10px"
          />

          <MenuList>
            <MenuItem>
              <NavLink to="/">Home</NavLink>
            </MenuItem>
            <MenuItem>
              <NavLink to="/About">About</NavLink>
            </MenuItem>
            <MenuItem>
              <NavLink to="/Project">Project</NavLink>
            </MenuItem>
            <MenuItem>
              <NavLink to="/Skills">Skills</NavLink>
            </MenuItem>
            <MenuItem>
              <NavLink to="/Contact">Contact</NavLink>
            </MenuItem>
          </MenuList>
        </Menu>
      ) : (
        <Flex justifyContent="space-around" w={{ md: "700px" }} mt="10px">
          <NavLink to="/">Home</NavLink>
          <NavLink to="/About">About</NavLink>
          <NavLink to="/Project">Project</NavLink>
          <NavLink to="/Skills">Skills</NavLink>
          <NavLink to="/Contact">Contact</NavLink>
        </Flex>
      )}
    </Box>
   
  </Box>
  );
}

export default Navbar;
